:root {
  --bg-main: #EDEDED;
  --bg-dark: #03071e;
  --bg-dark-modal: #181b34;
  --bg-white: #ffffff;
  --bg-gray: #CDCDCD;
  --bg-gray-2: #ADADAD;
  --bg-yellow: #FBFB00;
  --bg-darkyellow: #E1CD00;
  --bg-orange: #F4B100;
  --bg-green: #319966;
  --bg-green-2: #248957;
  --bg-purple: #8034e5;
  --bg-darkpurple: #68005B;
  --bg-red: #DF2E2E;
  --bg-darkred: #87002D;
  --bg-violet: #B40057;
  --bg-darkrose: #b41344;
  --bg-blue: #0060c7;
  --bg-lightblue: #05C4CD;
}

#root {
  font-family: 'Lato', sans-serif;
  background-color: var(--bg-main);
  background: #A770EF;
  background: -webkit-linear-gradient(to right, #FDB99B, #CF8BF3, #A770EF);
  background: linear-gradient(to right, #FDB99B, #CF8BF3, #A770EF);
}

/* ----- Scrollbar ----- */
@media (min-width: 600px) {
  #root {
    overflow-y: auto;
  }
  ::-webkit-scrollbar {
    width: 7px;
  }

  ::-webkit-scrollbar-track {
    background-color: var(--bg-gray);
  }

  ::-webkit-scrollbar-thumb {
    background-color: var(--bg-violet);
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: var(--bg-violet);
  }
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

p {
  line-height: 1.4;
  font-family: 'Noto Sans', system-ui, sans-serif;
}

.text-decoration-none {
  text-decoration: none;
}

.pointer:hover {
  cursor: pointer;
}

.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}

/*------------- Efecto de fondo en iconos -------------*/
.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.icon:hover {
  cursor: pointer;
}

.icon:hover:after {
  opacity: 1;
}

.icon:after {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 99999999999;
  pointer-events: none;
}

.icon.full-rounded:after {
  border-radius: 50%;
}

.icon.rounded-top-left:after {
  border-top-left-radius: 50%;
}

.icon.rounded-top-right:after {
  border-top-right-radius: 50%;
}

.icon.rounded-bottom-left:after {
  border-bottom-left-radius: 50%;
}

.icon.rounded-bottom-right:after {
  border-bottom-right-radius: 50%;
}

.icon.smooth.light-mode:after {
  background-color: rgba(0, 0, 0, .05);
}

.icon.smooth.dark-mode:after {
  background-color: rgba(255, 255, 255, .05);
}

.icon.light-mode:after {
  background-color: rgba(0, 0, 0, .1);
}

.icon.dark-mode:after {
  background-color: rgba(255, 255, 255, .1);
}

.icon-effect.radius-sm:hover:after {
  border-radius: 7px;
}

.w-100 {
  width: 100%;
}

.mb-0 {
  margin-bottom: 0;
}

.d-none {
  display: none;
}

.d-flex {
  display: flex;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.j-center {
  justify-content: center;
}

.j-around {
  justify-content: space-around;
}

.j-between {
  justify-content: space-between;
}

.align-items-center {
  align-items: center;
}

.border-none {
  border: none !important;
  outline: none !important;
}

.scale-button:active {
  transform: scale(85%);
}

.checkbox {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: table;
  font-size: 1em;
  padding: 3px 0;
  font-family: "Titillium Web", sans-serif;
}

.checkbox>.title:before {
  content: '';
  display: inline-flex;
  width: 15px;
  height: 15px;
  padding: 8.5px;
  border-radius: 3px;
  font-size: 11px;
  background-color: var(--bg-gray);
  margin-right: 7px;
}

.checkbox.active>.title:before {
  content: '\f00c';
  padding: 0 3px;
  font-family: 'Font Awesome 5 Pro';
  font-weight: 900;
  display: table;
  background-color: var(--bg-darkrose);
  color: var(--bg-white)
}